"use client"

import type React from "react"
import { useState, useEffect } from "react"
import { useSearchParams } from "next/navigation"
import {
  Search,
  Mail,
  Package,
  IndianRupee,
  Hash,
  Calendar,
  Truck,
  MessageSquare,
  X,
  CheckCircle,
  Send,
} from "lucide-react"
import { cookies } from 'next/headers';

// Define TypeScript interfaces
interface NegotiationDetail {
  negId: string
  yourAmount: string
  yourQty: string
  previewAmount: string
  previewQty: string
  deliveryRelatedInfo: string
  messages: string
  createdAt: string
}

interface QuotationData {
  _id: string
  productName: string
  pid: string
  hsnCode: string
  amount: string
  minQty: string
  commission: string
  measurement: string
  gstPercentage: string
  seller_email: string
  buyer_email: string
  reqId: string
  company: string
  pincode: string
  mobile: string
  created_at: string
  status: string
  description: string
  negotiation: NegotiationDetail[]
}

interface NegotiationData {
  productDetails: {
    productName: string
    productId: string
    hsnCode: string
    measurement: string
    gst: string
  }
  sellerInfo: {
    email: string
  }
  requestInfo: {
    requestId: string
    createdAt: string
  }
  negotiationDetails: {
    negotiationId: string
    negotiationAmount: string
    negotiationQuantity: string
    previewAmount: string
    previewQuantity: string
  }
  additionalInfo: {
    deliveryInfo: string
    description: string
  }
  status: string
}

// Helper components with TypeScript
const Input: React.FC<{
  value: string
  onChange: (e: React.ChangeEvent<HTMLInputElement>) => void
  placeholder: string
  className?: string
}> = ({ value, onChange, placeholder, className = "" }) => (
  <div className="relative">
    <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-5 w-5 text-gray-400" />
    <input
      type="text"
      value={value}
      onChange={onChange}
      placeholder={placeholder}
      className={`pl-10 pr-4 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 ${className}`}
    />
  </div>
)

const Button: React.FC<{
  onClick: () => void
  children: React.ReactNode
  className?: string
}> = ({ onClick, children, className = "" }) => (
  <button
    onClick={onClick}
    className={`px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 transition-colors ${className}`}
  >
    {children}
  </button>
)

const InfoRow: React.FC<{
  icon: React.ReactNode
  label: string
  value: string | number
}> = ({ icon, label, value }) => (
  <div className="flex items-center mb-2">
    {icon}
    <span className="font-semibold mr-2 ml-2">{label}:</span>
    <span>{value}</span>
  </div>
)

const NegotiationCard: React.FC<{
  data: QuotationData
  onClose: () => void
  onSendNegotiation: (data: NegotiationData) => void
  onUpdateStatus: (quotationId: string, status: string) => void
  onSendToCustomer: (quotationId: string, amount: string) => void
}> = ({ data, onClose, onSendNegotiation, onUpdateStatus, onSendToCustomer }) => {
  const [newAmount, setNewAmount] = useState<string>("")
  const [newStatus, setNewStatus] = useState<string>(data.status)
  const negotiation = data.negotiation[0]

  const handleSendNegotiation = () => {
    const negotiationData: NegotiationData = {
      productDetails: {
        productName: data.productName,
        productId: data.pid,
        hsnCode: data.hsnCode,
        measurement: data.measurement,
        gst: `${data.gstPercentage}%`,
      },
      sellerInfo: {
        email: data.seller_email,
      },
      requestInfo: {
        requestId: data.reqId,
        createdAt: data.created_at,
      },
      negotiationDetails: {
        negotiationId: negotiation.negId,
        negotiationAmount: newAmount || negotiation.yourAmount,
        negotiationQuantity: negotiation.yourQty,
        previewAmount: negotiation.previewAmount,
        previewQuantity: negotiation.previewQty,
      },
      additionalInfo: {
        deliveryInfo: negotiation.deliveryRelatedInfo,
        description: data.description,
      },
      status: newStatus,
    }

    onSendNegotiation(negotiationData)
  }

  const handleStatusUpdate = () => {
    onUpdateStatus(data._id, newStatus)
  }

  const handleSendToCustomer = () => {
    onSendToCustomer(data._id, newAmount || negotiation.yourAmount)
  }

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
      <div className="bg-white rounded-lg shadow-lg p-6 max-w-2xl w-full max-h-[90vh] overflow-y-auto thin relative">
        <button onClick={onClose} className="absolute top-4 right-4 text-gray-500 hover:text-gray-700">
          <X className="w-6 h-6" />
        </button>
        <h2 className="text-2xl font-bold mb-4">{data.productName}</h2>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div>
            <InfoRow icon={<Package className="w-5 h-5" />} label="Product ID" value={data.pid} />
            <InfoRow icon={<Hash className="w-5 h-5" />} label="HSN Code" value={data.hsnCode} />
            <InfoRow icon={<IndianRupee className="w-5 h-5" />} label="Amount" value={`₹${data.amount}`} />
            <InfoRow icon={<Package className="w-5 h-5" />} label="Min Quantity" value={data.minQty} />
            <InfoRow icon={<IndianRupee className="w-5 h-5" />} label="Commission" value={`${data.commission}%`} />
            <InfoRow icon={<Package className="w-5 h-5" />} label="Measurement" value={data.measurement} />
            <InfoRow icon={<IndianRupee className="w-5 h-5" />} label="GST" value={`${data.gstPercentage}%`} />
          </div>
          <div>
            <InfoRow icon={<Mail className="w-5 h-5" />} label="Seller Email" value={data.seller_email} />
            <InfoRow icon={<Mail className="w-5 h-5" />} label="Buyer Email" value={data.buyer_email} />
            <InfoRow icon={<Hash className="w-5 h-5" />} label="Request ID" value={data.reqId} />
            <InfoRow icon={<Package className="w-5 h-5" />} label="Company" value={data.company} />
            <InfoRow icon={<Hash className="w-5 h-5" />} label="Pincode" value={data.pincode} />
            <InfoRow icon={<Hash className="w-5 h-5" />} label="Mobile" value={data.mobile} />
            <InfoRow
              icon={<Calendar className="w-5 h-5" />}
              label="Created At"
              value={new Date(data.created_at).toLocaleString()}
            />
          </div>
        </div>

        <div className="mt-6">
          <h3 className="text-xl font-semibold mb-3">Negotiation Details</h3>
          <div className="bg-gray-100 p-4 rounded-md">
            <InfoRow icon={<Hash className="w-5 h-5" />} label="Negotiation ID" value={negotiation?.negId} />
            <InfoRow
              icon={<IndianRupee className="w-5 h-5" />}
              label="Negotiation Amount"
              value={`₹${negotiation.yourAmount}`}
            />
            <InfoRow icon={<Package className="w-5 h-5" />} label="Negotiation Quantity" value={negotiation.yourQty} />
            <InfoRow
              icon={<IndianRupee className="w-5 h-5" />}
              label="Preview Amount"
              value={`₹${negotiation.previewAmount}`}
            />
            <InfoRow icon={<Package className="w-5 h-5" />} label="Preview Quantity" value={negotiation.previewQty} />
            <InfoRow
              icon={<Truck className="w-5 h-5" />}
              label="Delivery Info"
              value={negotiation.deliveryRelatedInfo}
            />
            <InfoRow icon={<MessageSquare className="w-5 h-5" />} label="Messages" value={negotiation.messages} />
            <InfoRow
              icon={<Calendar className="w-5 h-5" />}
              label="Created At"
              value={new Date(negotiation.createdAt).toLocaleString()}
            />

            <div className="mt-4">
              <label htmlFor="newAmount" className="block text-sm font-medium text-gray-700">
                New Negotiation Amount
              </label>
              <div className="mt-1 relative rounded-md shadow-sm">
                <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                  <IndianRupee className="h-5 w-5 text-gray-400" />
                </div>
                <input
                  type="number"
                  id="newAmount"
                  value={newAmount}
                  onChange={(e) => setNewAmount(e.target.value)}
                  className="focus:ring-indigo-500 focus:border-indigo-500 block w-full pl-10 pr-12 sm:text-sm border-gray-300 rounded-md"
                  placeholder="Enter new amount"
                />
              </div>
            </div>
          </div>
        </div>

        <div className="mt-6">
          <h3 className="text-xl font-semibold mb-2">Status Update</h3>
          <div className="flex items-center space-x-4">
            <select
              value={newStatus}
              onChange={(e) => setNewStatus(e.target.value)}
              className="mt-1 block w-full pl-3 pr-10 py-2 text-base border-gray-300 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm rounded-md"
            >
              <option value="pending">Pending</option>
              <option value="accepted">Accepted</option>
              <option value="rejected">Rejected</option>
            </select>
            <Button onClick={handleStatusUpdate} className="flex items-center">
              <CheckCircle className="w-5 h-5 mr-2" /> Update Status
            </Button>
          </div>
        </div>

        <div className="mt-6 flex justify-between items-center">
          <Button onClick={handleSendNegotiation} className="flex items-center">
            <Send className="w-5 h-5 mr-2" /> Send Negotiation
          </Button>
          <Button onClick={handleSendToCustomer} className="flex items-center">
            <Mail className="w-5 h-5 mr-2" /> Send to Customer
          </Button>
        </div>
      </div>
    </div>
  )
}

const NegotiationPage = async () => {
  const [data, setData] = useState<QuotationData[]>([])
  const [isLoading, setIsLoading] = useState<boolean>(true)
  const [error, setError] = useState<string | null>(null)
  const [selectedQuotation, setSelectedQuotation] = useState<QuotationData | null>(null)
  const [searchQuery, setSearchQuery] = useState<string>("")

  const searchParams = useSearchParams()
  const reqId = searchParams.get("id")
  const cookieStore = await cookies()

  useEffect(() => {
    const fetchNegotiations = async () => {
      try {
        setIsLoading(true)
        const token = getCookie("token")

        const response = await fetch("http://localhost:3033/api/v1/negotiationAll", {
          method: "GET",
          headers: {
            Authorization: `Bearer ${token}`,
            "Content-Type": "application/json",
          },
        })

        if (!response.ok) {
          throw new Error(`Error: ${response.status}`)
        }

        const result = await response.json()
        setData(result)

        // If reqId is provided in the URL, find and select that quotation
        if (reqId) {
          const selectedQuote = result.find((quote: QuotationData) => quote.reqId === reqId)
          if (selectedQuote) {
            setSelectedQuotation(selectedQuote)
          }
        }
      } catch (err) {
        setError(err instanceof Error ? err.message : "An unknown error occurred")
      } finally {
        setIsLoading(false)
      }
    }

    fetchNegotiations()
  }, [reqId])

  const handleCardClick = (quotation: QuotationData) => {
    setSelectedQuotation(quotation)
  }

  const handleCloseModal = () => {
    setSelectedQuotation(null)
  }

  const handleSendNegotiation = async (negotiationData: NegotiationData) => {
    try {
      const token = getCookie("token")

      const response = await fetch("http://localhost:3033/api/v1/negotaiton", {
        method: "POST",
        headers: {
          Authorization: `Bearer ${token}`,
          "Content-Type": "application/json",
        },
        body: JSON.stringify(negotiationData),
      })

      if (!response.ok) {
        throw new Error(`Error: ${response.status}`)
      }

      handleCloseModal()
    } catch (error) {
      console.error("Failed to send negotiation:", error)
    }
  }

  const handleStatusUpdate = async (quotationId: string, newStatus: string) => {
    try {
      const token = getCookie("token")

      const response = await fetch(`http://localhost:3033/api/v1/admin/quotations/${quotationId}/status`, {
        method: "POST",
        headers: {
          Authorization: `Bearer ${token}`,
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ status: newStatus }),
      })

      if (!response.ok) {
        throw new Error(`Error: ${response.status}`)
      }

      console.log("Status updated successfully")
    } catch (error) {
      console.error("Failed to update status:", error)
    }
  }

  const handleSendToCustomer = async (quotationId: string, amount: string) => {
    try {
      const token = getCookie("token")

      const response = await fetch(`http://localhost:3033/api/v1/admin/quotations/${quotationId}/sendToCustomer`, {
        method: "POST",
        headers: {
          Authorization: `Bearer ${token}`,
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ amount }),
      })

      if (!response.ok) {
        throw new Error(`Error: ${response.status}`)
      }

      console.log("Sent to customer successfully")
    } catch (error) {
      console.error("Failed to send to customer:", error)
    }
  }

  if (isLoading) return <div>Loading...</div>
  if (error) return <div>Error: {error}</div>

  const filteredData = data.filter((quotation) =>
    quotation.productName.toLowerCase().includes(searchQuery.toLowerCase()),
  )

  return (
    <div className="p-4">
      <h1 className="text-2xl font-bold mb-4">Admin Customer Quotations</h1>
      <Input
        value={searchQuery}
        onChange={(e) => setSearchQuery(e.target.value)}
        placeholder="Search by Product Name"
        className="mb-4"
      />
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
        {filteredData.map((quotation) => (
          <div
            key={quotation._id}
            onClick={() => handleCardClick(quotation)}
            className="p-4 border border-gray-200 rounded-md cursor-pointer hover:shadow-lg transition-shadow"
          >
            <h2 className="text-xl font-semibold mb-2">{quotation.productName}</h2>
            <p>
              <span className="font-semibold">Quotation ID:</span> {quotation._id}
            </p>
            <p>
              <span className="font-semibold">Company:</span> {quotation.company}
            </p>
            <p>
              <span className="font-semibold">Status:</span> {quotation.status}
            </p>
          </div>
        ))}
      </div>
      {selectedQuotation && (
        <NegotiationCard
          data={selectedQuotation}
          onClose={handleCloseModal}
          onSendNegotiation={handleSendNegotiation}
          onUpdateStatus={handleStatusUpdate}
          onSendToCustomer={handleSendToCustomer}
        />
      )}
    </div>
  )
}

export default NegotiationPage
